import React from "react";
import {useLocation} from 'react-router-dom';

const AddNewMarker = () =>{
    const location = useLocation();
    const data = location.state.data;
    return(
        <div className="spacing">
            <div className="container">
                <div className="row">
                    <div className="col-xl-9 col-lg-10 col-md-11 mx-auto">
                        <input type="text" id="location-lat" value={data.lat}  readOnly className="sr-only"/>
                        <input type="text" id="location-lng" value={data.lng} readOnly className="sr-only"/>
                        <div className="row mgb--2">
                            <div className="col-md-6 mgb-2">
                                <div className="form-group">
                                    <label htmlFor="location-name">Location Name</label>
                                    <input type="text" id="location-name" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-md-6 mgb-2">
                                <div className="form-group">
                                    <label htmlFor="location-name">LandMark</label>
                                    <input type="text" id="landMark" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-md-6 mgb-2">
                                <div className="form-group">
                                    <label htmlFor="location-name">imges</label>
                                    <input type="file" id="image1" className="form-control"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AddNewMarker;
